import React from 'react';
import PropTypes from 'prop-types';
import CardNameSVG from './CardNameSVG';
import CardTypeFrameSVG from './CardTypeFrameSVG';
import FramePartsSVG from './FramePartsSVG';
import CostCircleSVG from './CostCircleSVG';
import PowerNumberSVG from './PowerNumberSVG';
import CardTextSVG from './CardTextSVG';
import ManaCircleSVG from './ManaCircleSVG';
import frameImage from '../assets/parts/dm4.svg';

const NormalCardFrameSVG = ({
    imageUrl,
    creatureData,
}) => {
    return (
        <svg width="252" height="344" viewBox="0 0 252 344" xmlns="http://www.w3.org/2000/svg">
            {/*  */}
            {imageUrl && (
                <image href={imageUrl} x="0" y="0" width="252" height="344" />
            )}

            {/*  */}
            <CardNameSVG
                x={126}
                y={24}
                cardName={creatureData.cardName}
                rubyText={creatureData.rubyText}
                race={creatureData.race}
                cardNameFontSize={creatureData.cardNameFontSize}
                rubyTextFontSize={creatureData.rubyTextFontSize}
                raceFontSize={creatureData.raceFontSize}
                cardNamePosition={creatureData.cardNamePosition}
                rubyTextPosition={creatureData.rubyTextPosition}
                racePosition={creatureData.racePosition}
                alignment={creatureData.alignment}
            />

            {/*  */}
            <CardTypeFrameSVG
                x={8.5}
                y={200}
                cardType={creatureData.cardType}
                civilizations={creatureData.civilizations}
            />

            {/*  */}
            <FramePartsSVG
                cardType={creatureData.cardType}
                startY={creatureData.evoMarkStartY}
                spacing={creatureData.evoMarkSpacing}
            />

            {/*  */}
            <CardTextSVG
                x={20}
                y={218}
                effectText={creatureData.effectText}
                flavorText={creatureData.flavorText}
                effectTextFontSize={creatureData.effectTextFontSize}
                flavorTextFontSize={creatureData.flavorTextFontSize}
            />

            {/*  */}
            <image href={frameImage} x="0" y="0" width="252" height="344" />

            {/*  */}
            {/*  */}
            <CostCircleSVG x={7} y={7} cost={creatureData.cost} civilizations={creatureData.civilizations} />
            {/*  */}
            <PowerNumberSVG number={creatureData.power} x={20} y={328} />
            {/*  */}
            <ManaCircleSVG
                x={108.3}
                y={308}
                creatureCivilizations={creatureData.civilizations}
            />
        </svg>
    );
};

// PropTypes
NormalCardFrameSVG.propTypes = {
    imageUrl: PropTypes.string, // URL
    creatureData: PropTypes.shape({
        cardName: PropTypes.string.isRequired, // 
        rubyText: PropTypes.string, // 
        race: PropTypes.string, // 
        cardNameFontSize: PropTypes.number, // 
        rubyTextFontSize: PropTypes.number, // 
        raceFontSize: PropTypes.number, // 
        cardNamePosition: PropTypes.shape({ x: PropTypes.number, y: PropTypes.number }), // 
        rubyTextPosition: PropTypes.shape({ x: PropTypes.number, y: PropTypes.number }), // 
        racePosition: PropTypes.shape({ x: PropTypes.number, y: PropTypes.number }), // 
        alignment: PropTypes.oneOf(['left', 'center', 'right']), // 
        cardType: PropTypes.string.isRequired, // 
        civilizations: PropTypes.arrayOf(PropTypes.string).isRequired, // 
        evoMarkStartY: PropTypes.number, // Y
        evoMarkSpacing: PropTypes.number, // 
        effectText: PropTypes.string, // 
        flavorText: PropTypes.string, // 
        effectTextFontSize: PropTypes.number, // 
        flavorTextFontSize: PropTypes.number, // 
        cost: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired, // 
        power: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired, // 
    }).isRequired,
};

export default NormalCardFrameSVG;