import React, { memo } from 'react';
import PropTypes from 'prop-types';

const CardNameSVG = ({
    x,
    y,
    cardName,
    rubyText,
    race,
    cardNameFontSize = 16,
    rubyTextFontSize = 8,
    raceFontSize = 7,
    cardNamePosition = { x: 0, y: 15.6 },
    rubyTextPosition = { x: 0, y: 0 },
    racePosition = { x: 0, y: 25 },
    alignment = 'center',
}) => {
    // 
    const textAnchor = {
        left: 'start',
        center: 'middle',
        right: 'end',
    }[alignment];

    return (
        // 
        <g transform={`translate(${x}, ${y})`}>
            {/*  */}
            {rubyText && (
                <TextElement
                    text={rubyText}
                    position={rubyTextPosition}
                    fontSize={rubyTextFontSize}
                    textAnchor={textAnchor}
                    strokeWidth={0.35}
                />
            )}
            {/*  */}
            <TextElement
                text={cardName}
                position={cardNamePosition}
                fontSize={cardNameFontSize}
                textAnchor={textAnchor}
                strokeWidth={0.7}
            />
            {/*  */}
            {race && (
                <TextElement
                    text={race}
                    position={racePosition}
                    fontSize={raceFontSize}
                    textAnchor={textAnchor}
                    strokeWidth={0.5}
                />
            )}
        </g>
    );
};

const TextElement = memo(({ text, position, fontSize, textAnchor, strokeWidth }) => (
    <text
        x={position.x}
        y={position.y}
        fontSize={fontSize}
        fontWeight="bold"
        fill="white"
        stroke="black"
        strokeWidth={strokeWidth}
        fontFamily="LineSeedJP"
        textAnchor={textAnchor}
    >
        {/*  */}
        {text.split('').map((char, index) => (
            <tspan key={index} dx={char === ' ' ? fontSize * 0.05 : 0}>
                {char === ' ' ? '' : char}
            </tspan>
        ))}
    </text>
));

CardNameSVG.propTypes = {
    x: PropTypes.number.isRequired, // X
    y: PropTypes.number.isRequired, // Y
    cardName: PropTypes.string.isRequired, // 
    rubyText: PropTypes.string, // 
    race: PropTypes.string, // 
    cardNameFontSize: PropTypes.number, // 
    rubyTextFontSize: PropTypes.number, // 
    raceFontSize: PropTypes.number, // 
    cardNamePosition: PropTypes.shape({
        x: PropTypes.number, // X
        y: PropTypes.number, // Y
    }),
    rubyTextPosition: PropTypes.shape({
        x: PropTypes.number, // X
        y: PropTypes.number, // Y
    }),
    racePosition: PropTypes.shape({
        x: PropTypes.number, // X
        y: PropTypes.number, // Y
    }),
    alignment: PropTypes.oneOf(['left', 'center', 'right']), // 
};

TextElement.propTypes = {
    text: PropTypes.string.isRequired, // 
    position: PropTypes.shape({
        x: PropTypes.number, // X
        y: PropTypes.number, // Y
    }).isRequired,
    fontSize: PropTypes.number.isRequired, // 
    textAnchor: PropTypes.string.isRequired, // 
    strokeWidth: PropTypes.number.isRequired, // 
};

export default memo(CardNameSVG);