// SliderModal.js
import React from 'react';
import PropTypes from 'prop-types';
import { Modal, Box, Button, Slider } from '@mui/material';
import './SliderModal.css';

// 
const SliderInput = ({ label, name, value, onChange, min, max, step = 1 }) => (
    <div className="slider-input">
        <label>{label}: {value}</label>
        <Slider
            value={value}
            name={name}
            onChange={(e, newValue) => onChange(name, newValue)}
            min={min}
            max={max}
            step={step}
        />
    </div>
);

const SliderModal = ({ open, handleClose, formData, handleSliderChange, isCreature, isTwinImpact }) => {
    return (
        <Modal
            open={open}
            onClose={handleClose}
            aria-labelledby="slider-modal-title"
            aria-describedby="slider-modal-description"
        >
            <Box sx={modalStyle}>
                {/*  */}
                <h2 id="slider-modal-title">
                    {isTwinImpact ? (isCreature ? '' : '') : ''}
                </h2>

                {/*  */}
                <div className="slider-row">
                    <SliderInput
                        label="X"
                        name={isCreature ? 'creatureCardNamePositionX' : 'spellCardNamePositionX'}
                        value={isCreature ? formData.creatureCardNamePositionX : formData.spellCardNamePositionX}
                        onChange={handleSliderChange}
                        min={-100}
                        max={100}
                        step={1}
                    />
                    <SliderInput
                        label="Y"
                        name={isCreature ? 'creatureCardNamePositionY' : 'spellCardNamePositionY'}
                        value={isCreature ? formData.creatureCardNamePositionY : formData.spellCardNamePositionY}
                        onChange={handleSliderChange}
                        min={-100}
                        max={100}
                        step={1}
                    />
                    <SliderInput
                        label=""
                        name={isCreature ? 'creatureCardNameFontSize' : 'spellCardNameFontSize'}
                        value={isCreature ? formData.creatureCardNameFontSize : formData.spellCardNameFontSize}
                        onChange={handleSliderChange}
                        min={10}
                        max={30}
                        step={0.5}
                    />
                </div>

                {/*  */}
                <div className="slider-row">
                    <SliderInput
                        label="X"
                        name={isCreature ? 'creatureRubyTextPositionX' : 'spellRubyTextPositionX'}
                        value={isCreature ? formData.creatureRubyTextPositionX : formData.spellRubyTextPositionX}
                        onChange={handleSliderChange}
                        min={-100}
                        max={100}
                        step={1}
                    />
                    <SliderInput
                        label="Y"
                        name={isCreature ? 'creatureRubyTextPositionY' : 'spellRubyTextPositionY'}
                        value={isCreature ? formData.creatureRubyTextPositionY : formData.spellRubyTextPositionY}
                        onChange={handleSliderChange}
                        min={-100}
                        max={100}
                        step={1}
                    />
                    <SliderInput
                        label=""
                        name={isCreature ? 'creatureRubyTextFontSize' : 'spellRubyTextFontSize'}
                        value={isCreature ? formData.creatureRubyTextFontSize : formData.spellRubyTextFontSize}
                        onChange={handleSliderChange}
                        min={5}
                        max={20}
                        step={0.5}
                    />
                </div>

                {/*  */}
                <div className="slider-row">
                    <SliderInput
                        label="X"
                        name={isCreature ? 'creatureRacePositionX' : 'spellRacePositionX'}
                        value={isCreature ? formData.creatureRacePositionX : formData.spellRacePositionX}
                        onChange={handleSliderChange}
                        min={-100}
                        max={100}
                        step={1}
                    />
                    <SliderInput
                        label="Y"
                        name={isCreature ? 'creatureRacePositionY' : 'spellRacePositionY'}
                        value={isCreature ? formData.creatureRacePositionY : formData.spellRacePositionY}
                        onChange={handleSliderChange}
                        min={-100}
                        max={100}
                        step={1}
                    />
                    <SliderInput
                        label=""
                        name={isCreature ? 'creatureRaceFontSize' : 'spellRaceFontSize'}
                        value={isCreature ? formData.creatureRaceFontSize : formData.spellRaceFontSize}
                        onChange={handleSliderChange}
                        min={5}
                        max={20}
                        step={0.5}
                    />
                </div>

                {/*  */}
                <div className="slider-row">
                    <SliderInput
                        label="Y"
                        name={isCreature ? 'creatureEvoMarkStartY' : 'spellEvoMarkStartY'}
                        value={isCreature ? formData.creatureEvoMarkStartY : formData.spellEvoMarkStartY}
                        onChange={handleSliderChange}
                        min={30}
                        max={240}
                        step={1}
                    />
                    {/* 2 */}
                    {(isCreature ? formData.creatureEvoMarkCount : formData.spellEvoMarkCount) > 1 && (
                        <SliderInput
                            label=""
                            name={isCreature ? 'creatureEvoMarkSpacing' : 'spellEvoMarkSpacing'}
                            value={isCreature ? formData.creatureEvoMarkSpacing : formData.spellEvoMarkSpacing}
                            onChange={handleSliderChange}
                            min={0}
                            max={100}
                            step={1}
                        />
                    )}
                </div>

                {/*  */}
                <Button variant="contained" onClick={handleClose} sx={{ mt: 2 }}>
                    
                </Button>
            </Box>
        </Modal>
    );
};

// 
const modalStyle = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: '80%',
    bgcolor: 'background.paper',
    boxShadow: 24,
    p: 4,
};

// PropTypes
SliderModal.propTypes = {
    open: PropTypes.bool.isRequired, // 
    handleClose: PropTypes.func.isRequired, // 
    formData: PropTypes.object.isRequired, // 
    handleSliderChange: PropTypes.func.isRequired, // 
    isCreature: PropTypes.bool.isRequired, // 
    isTwinImpact: PropTypes.bool, // 
};

export default SliderModal;
