import React from 'react';
import PropTypes from 'prop-types';
import Zero from '../assets/numbers/0.svg';
import One from '../assets/numbers/1.svg';
import Two from '../assets/numbers/2.svg';
import Three from '../assets/numbers/3.svg';
import Four from '../assets/numbers/4.svg';
import Five from '../assets/numbers/5.svg';
import Six from '../assets/numbers/6.svg';
import Seven from '../assets/numbers/7.svg';
import Eight from '../assets/numbers/8.svg';
import Nine from '../assets/numbers/9.svg';

// 
const numberImages = [Zero, One, Two, Three, Four, Five, Six, Seven, Eight, Nine];

const CostNumberSVG = ({ number, x, y }) => {
    // 
    const digits = String(number).split('');
    const digitCount = digits.length;

    // 
    const imageWidth = digitCount > 2 ? 12 : 14;
    const imageHeight = digitCount > 2 ? 18 : 21;
    const totalWidth = digitCount * imageWidth - (digitCount - 1.5) * 3;

    let currentX = x - totalWidth / 2;

    return (
        // 
        <g>
            {digits.map((digit, index) => {
                const imgSrc = numberImages[parseInt(digit, 10)];
                // 1
                const isOne = digit === '1';
                const adjustedImageWidth = isOne ? imageWidth - 2 : imageWidth;
                const adjustedImageHeight = isOne ? imageHeight - 1 : imageHeight;
                const imageElement = (
                    <image
                        key={index}
                        href={imgSrc}
                        x={currentX}
                        y={y - adjustedImageHeight / 2}
                        width={adjustedImageWidth}
                        height={adjustedImageHeight}
                    />
                );
                currentX += isOne ? imageWidth - 3 : imageWidth - 2; // 1
                return imageElement;
            })}
        </g>
    );
};

// PropTypes
CostNumberSVG.propTypes = {
    number: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired, // 
    x: PropTypes.number.isRequired, // X
    y: PropTypes.number.isRequired, // Y
};

export default CostNumberSVG;