// TwinImpactCardFormWithSVGDownload.js

import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import { Button } from '@mui/material';
import { civilizations } from '../utils/CardProperties';
import TwinImpactCardFrameSVG from './TwinImpactCardFrameSVG';
import NormalCardFrameSVG from './NormalCardFrameSVG';
import TwinImpactImageUploadAndCrop from './TwinImpactImageUploadAndCrop';
import ImageUploadAndCrop from './ImageUploadAndCrop';
import CardPartForm from './CardPartForm';
import CivilizationCheckboxGroup from './CivilizationCheckboxGroup';
import { toPng } from 'html-to-image';
import './CardFormWithSVGDownload.css';
import ManualModal from './ManualModal';

// TwinImpactCardFormWithSVGDownload 
const TwinImpactCardFormWithSVGDownload = () => {

    // 
    const [formData, setFormData] = useState({
        // 
        creatureCardName: '',
        creatureRubyText: '',
        creatureRace: '',
        creatureCost: 0,
        creaturePower: 0,
        creatureCardType: '',
        creatureEffectText: '',
        creatureFlavorText: '',
        creatureCivilization: [],
        creatureCardNameFontSize: 16,
        creatureEffectTextFontSize: 12,
        creatureFlavorTextFontSize: 6,
        creatureRubyTextFontSize: 8,
        creatureRaceFontSize: 7,
        creatureCardNamePositionX: 0,
        creatureCardNamePositionY: 15.6,
        creatureRubyTextPositionX: 0,
        creatureRubyTextPositionY: 0,
        creatureRacePositionX: 0,
        creatureRacePositionY: 25,

        // 
        spellCardName: '',
        spellRubyText: '',
        spellRace: '',
        spellCost: 0,
        spellPower: 0, // 
        spellCardType: '',
        spellEffectText: '',
        spellCivilization: [],
        spellCardNameFontSize: 16,
        spellEffectTextFontSize: 12,
        spellRubyTextFontSize: 8,
        spellRaceFontSize: 7,
        spellCardNamePositionX: 0,
        spellCardNamePositionY: 15.6,
        spellRubyTextPositionX: 0,
        spellRubyTextPositionY: 0,
        spellRacePositionX: 0,
        spellRacePositionY: 25,
    });

    const [isTwinImpact, setIsTwinImpact] = useState(false);
    const [creatureImageUrl, setCreatureImageUrl] = useState(null);
    const [spellImageUrl, setSpellImageUrl] = useState(null);
    const [isMobile, setIsMobile] = useState(false);
    const [manualOpen, setManualOpen] = useState(false);
    const cardRef = useRef(null);

    // 
    useEffect(() => {
        const handleResize = () => {
            const currentWidth = window.innerWidth;
            setIsMobile(currentWidth <= 767);
        };

        handleResize();
        window.addEventListener('resize', handleResize);

        return () => window.removeEventListener('resize', handleResize);
    }, []);

    // 
    const handleCivilizationChange = (e, isCreature) => {
        const { value, checked } = e.target;
        setFormData((prevState) => {
            const selectedCivilizations = isCreature ? prevState.creatureCivilization : prevState.spellCivilization;
            const updatedCivilizations = checked
                ? [...selectedCivilizations, value]
                : selectedCivilizations.filter((civ) => civ !== value);

            return {
                ...prevState,
                [isCreature ? 'creatureCivilization' : 'spellCivilization']: updatedCivilizations,
            };
        });
    };

    // 
    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({
            ...formData,
            [name]: value,
        });
    };

    // 
    const handleSliderChange = (name, newValue) => {
        setFormData((prevState) => ({
            ...prevState,
            [name]: newValue,
        }));
    };

    // 
    const handleCreatureImageUpload = (croppedImage) => {
        setCreatureImageUrl(croppedImage);
    };

    // 
    const handleSpellImageUpload = (croppedImage) => {
        setSpellImageUrl(croppedImage);
    };

    // PNG
    const downloadCardAsPNG = () => {
        if (cardRef.current) {
            const cardElement = cardRef.current;

            toPng(cardElement, {
                filter: (node) => node.tagName !== 'button' && node.tagName !== 'form',
                quality: 1,
                width: cardElement.clientWidth,
                height: cardElement.clientHeight,
                style: {
                    backgroundColor: 'transparent',
                },
            })
                .then((dataUrl) => {
                    const link = document.createElement('a');
                    link.download = `${formData.creatureCardName || 'twin_impact_card'}.png`;
                    link.href = dataUrl;
                    link.click();
                })
                .catch((error) => {
                    console.error('PNG', error);
                });
        }
    };

    // 
    const displayCardInNewTab = () => {
        if (cardRef.current) {
            const cardElement = cardRef.current;

            toPng(cardElement, {
                filter: (node) => node.tagName !== 'button' && node.tagName !== 'form',
                quality: 1,
                width: cardElement.clientWidth,
                height: cardElement.clientHeight,
                style: {
                    backgroundColor: 'transparent',
                },
            })
                .then((dataUrl) => {
                    const newTab = window.open();
                    if (newTab) {
                        newTab.document.body.innerHTML = `<img src="${dataUrl}" style="max-width:100%">`;
                    } else {
                        console.error('');
                    }
                })
                .catch((error) => {
                    console.error('PNG', error);
                });
        }
    };

    return (
        <div className="card-form-container">
            <div className="form-and-preview">
                <form className="card-form">
                    {/* isTwinImpact  */}
                    {isTwinImpact ? (
                        <>
                            {/*  */}
                            <CardPartForm
                                formData={formData}
                                handleSliderChange={handleSliderChange}
                                handleChange={handleChange}
                                isCreature
                            />
                            <CivilizationCheckboxGroup
                                civilizations={civilizations}
                                selectedCivilizations={formData.creatureCivilization}
                                onChange={(e) => handleCivilizationChange(e, true)}
                            />

                            {/*  */}
                            <CardPartForm
                                formData={formData}
                                handleSliderChange={handleSliderChange}
                                handleChange={handleChange}
                                isCreature={false}
                            />
                            <CivilizationCheckboxGroup
                                civilizations={civilizations}
                                selectedCivilizations={formData.spellCivilization}
                                onChange={(e) => handleCivilizationChange(e, false)}
                            />
                        </>
                    ) : (
                        <>
                            {/*  */}
                            <CardPartForm
                                formData={formData}
                                handleSliderChange={handleSliderChange}
                                handleChange={handleChange}
                                isCreature
                            />
                            <CivilizationCheckboxGroup
                                civilizations={civilizations}
                                selectedCivilizations={formData.creatureCivilization}
                                onChange={(e) => handleCivilizationChange(e, true)}
                            />
                        </>
                    )}
                </form>

                {/*  */}
                <div className="preview-container">
                    <h2></h2>

                    {/*  */}
                    {isTwinImpact ? (
                        <TwinImpactImageUploadAndCrop
                            onCreatureImageUpload={handleCreatureImageUpload}
                            onSpellImageUpload={handleSpellImageUpload}
                        />
                    ) : (
                        <ImageUploadAndCrop
                            onImageUpload={handleCreatureImageUpload}
                        />
                    )}

                    {/*  */}
                    {creatureImageUrl && (isTwinImpact ? spellImageUrl : true) && (
                        <div ref={cardRef} className="card-preview-frame">
                            {isTwinImpact ? (
                                <TwinImpactCardFrameSVG
                                    creatureImageUrl={creatureImageUrl}
                                    spellImageUrl={spellImageUrl}
                                    creatureData={{
                                        cardName: formData.creatureCardName || '',
                                        rubyText: formData.creatureRubyText || '',
                                        race: formData.creatureRace || '',
                                        cost: formData.creatureCost || 0,
                                        power: formData.creaturePower || 0,
                                        cardType: formData.creatureCardType || '',
                                        evoMarkStartY: formData.creatureEvoMarkStartY || 71,
                                        evoMarkSpacing: formData.creatureEvoMarkSpacing || 18,
                                        evoMarkCount: formData.creatureEvoMarkCount || 0,
                                        effectText: formData.creatureEffectText || '',
                                        flavorText: formData.creatureFlavorText || '',
                                        civilizations: formData.creatureCivilization || [],
                                        cardNameFontSize: formData.creatureCardNameFontSize || 16,
                                        cardNamePosition: {
                                            x: parseFloat(formData.creatureCardNamePositionX) || 0,
                                            y: parseFloat(formData.creatureCardNamePositionY) || 15.6,
                                        },
                                        rubyTextFontSize: formData.creatureRubyTextFontSize || 8,
                                        rubyTextPosition: {
                                            x: parseFloat(formData.creatureRubyTextPositionX) || 0,
                                            y: parseFloat(formData.creatureRubyTextPositionY) || 0,
                                        },
                                        raceFontSize: formData.creatureRaceFontSize || 7,
                                        racePosition: {
                                            x: parseFloat(formData.creatureRacePositionX) || 0,
                                            y: parseFloat(formData.creatureRacePositionY) || 25,
                                        },
                                    }}
                                    spellData={{
                                        cardName: formData.spellCardName || '',
                                        rubyText: formData.spellRubyText || '',
                                        race: formData.spellRace || '',
                                        cost: formData.spellCost || 0,
                                        cardType: formData.spellCardType || '',
                                        evoMarkStartY: formData.spellEvoMarkStartY || 215,
                                        evoMarkSpacing: formData.spellEvoMarkSpacing || 20,
                                        evoMarkCount: formData.spellEvoMarkCount || 0,
                                        effectText: formData.spellEffectText || '',
                                        civilizations: formData.spellCivilization || [],
                                        cardNameFontSize: formData.spellCardNameFontSize || 16,
                                        cardNamePosition: {
                                            x: parseFloat(formData.spellCardNamePositionX) || 0,
                                            y: parseFloat(formData.spellCardNamePositionY) || 15.6,
                                        },
                                        rubyTextFontSize: formData.spellRubyTextFontSize || 8,
                                        rubyTextPosition: {
                                            x: parseFloat(formData.spellRubyTextPositionX) || 0,
                                            y: parseFloat(formData.spellRubyTextPositionY) || 0,
                                        },
                                        raceFontSize: formData.spellRaceFontSize || 7,
                                        racePosition: {
                                            x: parseFloat(formData.spellRacePositionX) || 0,
                                            y: parseFloat(formData.spellRacePositionY) || 25,
                                        },
                                    }}
                                />
                            ) : (
                                <NormalCardFrameSVG
                                    imageUrl={creatureImageUrl}
                                    creatureData={{
                                        cardName: formData.creatureCardName || '',
                                        rubyText: formData.creatureRubyText || '',
                                        race: formData.creatureRace || '',
                                        cost: formData.creatureCost || 0,
                                        power: formData.creaturePower || 0,
                                        cardType: formData.creatureCardType || '',
                                        evoMarkStartY: formData.creatureEvoMarkStartY || 160,
                                        evoMarkSpacing: formData.creatureEvoMarkSpacing || 25,
                                        evoMarkCount: formData.creatureEvoMarkCount || 0,
                                        effectText: formData.creatureEffectText || '',
                                        flavorText: formData.creatureFlavorText || '',
                                        civilizations: formData.creatureCivilization || [],
                                        cardNameFontSize: formData.creatureCardNameFontSize || 16,
                                        cardNamePosition: {
                                            x: parseFloat(formData.creatureCardNamePositionX) || 0,
                                            y: parseFloat(formData.creatureCardNamePositionY) || 15.6,
                                        },
                                        rubyTextFontSize: formData.creatureRubyTextFontSize || 8,
                                        rubyTextPosition: {
                                            x: parseFloat(formData.creatureRubyTextPositionX) || 0,
                                            y: parseFloat(formData.creatureRubyTextPositionY) || 0,
                                        },
                                        raceFontSize: formData.creatureRaceFontSize || 7,
                                        racePosition: {
                                            x: parseFloat(formData.creatureRacePositionX) || 0,
                                            y: parseFloat(formData.creatureRacePositionY) || 25,
                                        }
                                    }}
                                />
                            )}
                        </div>
                    )}

                    {/*  */}
                    {isMobile ? (
                        <Button variant="contained" color="primary" onClick={displayCardInNewTab}>
                            
                        </Button>
                    ) : (
                        <Button variant="contained" color="primary" onClick={downloadCardAsPNG}>
                            PNG
                        </Button>
                    )}
                </div>
            </div>

            {/*  */}
            <Button
                variant="contained"
                color="primary"
                onClick={() => setIsTwinImpact(!isTwinImpact)}
                style={{ position: 'fixed', bottom: '70px', right: '10px' }}
            >
                {isTwinImpact ? '' : ''}
            </Button>

            {/*  */}
            <Button
                variant="outlined"
                color="secondary"
                onClick={() => setManualOpen(true)}
                style={{ position: 'fixed', bottom: '10px', right: '10px' }}
            >
                
            </Button>

            {/*  */}
            <ManualModal open={manualOpen} handleClose={() => setManualOpen(false)} />
        </div>
    );
};

// CardPartForm  PropTypes 
CardPartForm.propTypes = {
    formData: PropTypes.object.isRequired,
    handleSliderChange: PropTypes.func.isRequired,
    handleChange: PropTypes.func.isRequired,
    isCreature: PropTypes.bool.isRequired,
};

// CivilizationCheckboxGroup  PropTypes 
CivilizationCheckboxGroup.propTypes = {
    civilizations: PropTypes.array.isRequired,
    selectedCivilizations: PropTypes.array.isRequired,
    onChange: PropTypes.func.isRequired,
};

// TwinImpactCardFrameSVG  PropTypes 
TwinImpactCardFrameSVG.propTypes = {
    creatureImageUrl: PropTypes.string.isRequired,
    spellImageUrl: PropTypes.string.isRequired,
    creatureData: PropTypes.object.isRequired,
    spellData: PropTypes.object.isRequired,
};

// NormalCardFrameSVG  PropTypes 
NormalCardFrameSVG.propTypes = {
    imageUrl: PropTypes.string.isRequired,
    creatureData: PropTypes.object.isRequired,
};

export default TwinImpactCardFormWithSVGDownload;
