import React, { useState } from 'react';
import { Button } from '@mui/material';
import Cropper from 'react-cropper';
import 'cropperjs/dist/cropper.css';
import './TwinImpactImageUploadAndCrop.css'; // CSS

const TwinImpactImageUploadAndCrop = ({ onCreatureImageUpload, onSpellImageUpload }) => {
    const [creatureImage, setCreatureImage] = useState(null);
    const [spellImage, setSpellImage] = useState(null);
    const [creatureCropper, setCreatureCropper] = useState(null);
    const [spellCropper, setSpellCropper] = useState(null);

    // 
    const handleFileChange = (e, setImage) => {
        const files = e.dataTransfer ? e.dataTransfer.files : e.target.files;
        if (files && files.length > 0) {
            const reader = new FileReader();
            reader.onload = () => setImage(reader.result);
            reader.readAsDataURL(files[0]);
        }
    };

    // 
    const handleCroppedCreatureImage = () => {
        if (creatureCropper) {
            const croppedImage = creatureCropper.getCroppedCanvas().toDataURL();
            onCreatureImageUpload(croppedImage);
        }
    };

    // 
    const handleCroppedSpellImage = () => {
        if (spellCropper) {
            const croppedImage = spellCropper.getCroppedCanvas().toDataURL();
            onSpellImageUpload(croppedImage);
        }
    };

    return (
        <div className="twin-impact-image-upload-container">
            {/*  */}
            <h3></h3>
            <input type="file" onChange={(e) => handleFileChange(e, setCreatureImage)} accept="image/*" />
            {creatureImage && (
                <div className="cropper-container">
                    <Cropper
                        src={creatureImage}
                        style={{ height: 200, width: '100%' }}
                        aspectRatio={252 / 344} // 
                        guides={false}
                        cropBoxResizable
                        viewMode={1}
                        onInitialized={(instance) => setCreatureCropper(instance)}
                    />
                    <Button
                        variant="contained"
                        color="primary"
                        onClick={handleCroppedCreatureImage}
                        sx={{
                            '&:hover': {
                                backgroundColor: '#45a049', // 
                            },
                        }}
                    >
                        
                    </Button>
                </div>
            )}

            {/*  */}
            <h3></h3>
            <input type="file" onChange={(e) => handleFileChange(e, setSpellImage)} accept="image/*" />
            {spellImage && (
                <div className="cropper-container">
                    <Cropper
                        src={spellImage}
                        style={{ height: 200, width: '100%' }}
                        aspectRatio={252 / 140} // 
                        guides={false}
                        cropBoxResizable
                        viewMode={1}
                        onInitialized={(instance) => setSpellCropper(instance)}
                    />
                    <Button
                        variant="contained"
                        color="primary"
                        onClick={handleCroppedSpellImage}
                        sx={{
                            '&:hover': {
                                backgroundColor: '#45a049', // 
                            },
                        }}
                    >
                        
                    </Button>
                </div>
            )}
        </div>
    );
};

export default TwinImpactImageUploadAndCrop;