// CardPartForm.js
import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { TextField, Button } from '@mui/material';
import SliderModal from './SliderModal';
import { cardTypes } from '../utils/CardProperties';
import './CardPartForm.css';

const CardPartForm = ({ formData, handleChange, handleSliderChange, isCreature = true }) => {
    const [modalOpen, setModalOpen] = useState(false);

    // 
    const handleOpenModal = () => setModalOpen(true);
    // 
    const handleCloseModal = () => setModalOpen(false);

    // 
    const handleCardTypeChange = (e) => {
        const { value } = e.target;
        handleChange(e);

        // 
        const evolutionTypes = {
            '': 1,
            'V': 2,
            'GV': 3,
            '': 4,
            '': 1,
            '': 1,
            'S-MAX': 1
        };

        const evoMarkCount = evolutionTypes[value] || 0;

        // 
        handleSliderChange(isCreature ? 'creatureEvoMarkCount' : 'spellEvoMarkCount', evoMarkCount);
    };

    return (
        <>
            <h2>{isCreature ? '1' : '2'}</h2>

            {/*  */}
            <Button variant="outlined" onClick={handleOpenModal} sx={{ mb: 2 }}>
                {isCreature ? '' : ''}
            </Button>

            {/*  */}
            <SliderModal
                open={modalOpen}
                handleClose={handleCloseModal}
                formData={formData}
                handleSliderChange={handleSliderChange}
                isCreature={isCreature}
            />

            {/*  */}
            <TextField
                label=""
                name={isCreature ? 'creatureCardName' : 'spellCardName'}
                value={isCreature ? formData.creatureCardName : formData.spellCardName}
                onChange={handleChange}
                fullWidth
                margin="normal"
            />
            {/*  */}
            <TextField
                label=""
                name={isCreature ? 'creatureRubyText' : 'spellRubyText'}
                value={isCreature ? formData.creatureRubyText : formData.spellRubyText}
                onChange={handleChange}
                fullWidth
                margin="normal"
            />
            {/*  */}
            <TextField
                label=""
                name={isCreature ? 'creatureRace' : 'spellRace'}
                value={isCreature ? formData.creatureRace : formData.spellRace}
                onChange={handleChange}
                fullWidth
                margin="normal"
            />

            {/*  */}
            <div className="custom-select-container">
                <label className="custom-label"></label>
                <select
                    name={isCreature ? 'creatureCardType' : 'spellCardType'}
                    value={isCreature ? formData.creatureCardType : formData.spellCardType}
                    onChange={handleCardTypeChange}
                    className="custom-select"
                >
                    <option value=""></option>
                    {cardTypes.map((parentType) => (
                        <optgroup key={parentType.value} label={parentType.label} className="custom-optgroup">
                            <option value={parentType.value}>{parentType.label}</option>
                            {parentType.children &&
                                parentType.children.map((childType) => (
                                    <option key={childType.value} value={childType.value}>
                                        {childType.label}
                                    </option>
                                ))}
                        </optgroup>
                    ))}
                </select>
            </div>

            {/*  */}
            <div className="input-row">
                <div className="input-half">
                    <TextField
                        label=""
                        name={isCreature ? 'creatureCost' : 'spellCost'}
                        value={isCreature ? formData.creatureCost : formData.spellCost}
                        onChange={handleChange}
                        fullWidth
                        margin="normal"
                        type="number"
                    />
                </div>
                <div className="input-half">
                    <TextField
                        label=""
                        name={isCreature ? 'creaturePower' : 'spellPower'}
                        value={isCreature ? formData.creaturePower : formData.spellPower}
                        onChange={handleChange}
                        fullWidth
                        margin="normal"
                        type="number"
                    />
                </div>
            </div>

            {/*  */}
            <TextField
                label=""
                name={isCreature ? 'creatureEffectText' : 'spellEffectText'}
                value={isCreature ? formData.creatureEffectText : formData.spellEffectText}
                onChange={handleChange}
                fullWidth
                multiline
                rows={4}
                margin="normal"
            />

            {/*  */}
            <TextField
                label=""
                name={isCreature ? 'creatureFlavorText' : 'spellFlavorText'}
                value={isCreature ? formData.creatureFlavorText : formData.spellFlavorText}
                onChange={handleChange}
                fullWidth
                multiline
                rows={2}
                margin="normal"
            />
        </>
    );
};

// PropTypes
CardPartForm.propTypes = {
    formData: PropTypes.object.isRequired, // 
    handleChange: PropTypes.func.isRequired, // 
    handleSliderChange: PropTypes.func.isRequired, // 
    isCreature: PropTypes.bool, // 
};

export default CardPartForm;