import React from 'react';
import PropTypes from 'prop-types';
import { getColor } from '../utils/getColor';
import CostNumberSVG from './CostNumberSVG';

const CostCircleSVG = ({ x, y, cost, civilizations }) => {
    // civilizations  undefined 
    const validCivilizations = Array.isArray(civilizations) ? civilizations : [];

    // 
    const filteredCivilizations = validCivilizations.filter(civ => civ !== '');
    const finalCivilizations = filteredCivilizations.length > 0 ? filteredCivilizations : validCivilizations;

    const circleRadius = 8; // 
    const circleCircumference = 2 * Math.PI * circleRadius; // 

    // 
    const getStrokeDashArray = () => {
        const civCount = finalCivilizations.length;
        const dashLength = circleCircumference / civCount; // 
        return `${dashLength} ${circleCircumference - dashLength}`;
    };

    // 
    const getCivilizationSegments = () => {
        const civCount = finalCivilizations.length;
        const dashLength = circleCircumference / civCount;

        // 45-90
        const rotationAngle = civCount % 2 === 0 ? 45 : -90;

        return finalCivilizations.map((civ, index) => {
            const offset = -index * dashLength; // 
            return (
                <circle
                    key={civ}
                    cx="17" // X
                    cy="17" // Y
                    r={circleRadius} // 
                    fill="none" // 
                    stroke={getColor(civ)} // 
                    strokeWidth="16" // 
                    strokeDasharray={getStrokeDashArray()} // 
                    strokeDashoffset={offset} // 
                    transform={`rotate(${rotationAngle} 17 17)`} // 45-90
                />
            );
        });
    };

    return (
        <g transform={`translate(${x}, ${y})`}>
            {/*  */}
            <circle
                cx="17"
                cy="17"
                r={circleRadius}
                fill="black"
                stroke="black"
                strokeWidth="18" // 
            />
            {/*  */}
            {getCivilizationSegments()}
            {/*  */}
            <CostNumberSVG number={cost} x={17} y={17} isCost />
        </g>
    );
};

// PropTypes
CostCircleSVG.propTypes = {
    x: PropTypes.number.isRequired, // X
    y: PropTypes.number.isRequired, // Y
    cost: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired, // 
    civilizations: PropTypes.arrayOf(PropTypes.string), // 
};

export default CostCircleSVG;