import React, { memo } from 'react';
import PropTypes from 'prop-types';
import TextWithIconsSVG from './TextWithIconsSVG';

const CardTextSVG = ({ x, y, effectText, flavorText, effectTextFontSize, flavorTextFontSize }) => {
    return (
        <g transform={`translate(${x}, ${y})`}>
            {/*  */}
            <TextWithIconsSVG
                text={effectText}
                x={0}
                y={0}
                fontSize={effectTextFontSize || 8.4} // 
            />

            {/*  */}
            <text
                x="0"
                y="80"
                fontSize={flavorTextFontSize || 6}
                fill="gray"
                fontStyle="italic"
                fontFamily="LineSeedJP"
            >
                {flavorText}
            </text>
        </g>
    );
};

CardTextSVG.propTypes = {
    x: PropTypes.number.isRequired,
    y: PropTypes.number.isRequired,
    effectText: PropTypes.string.isRequired,
    flavorText: PropTypes.string,
    effectTextFontSize: PropTypes.number,
    flavorTextFontSize: PropTypes.number,
};


export default memo(CardTextSVG);
