import React, { useState, useRef } from 'react';
import PropTypes from 'prop-types';
import Cropper from 'react-cropper';
import 'cropperjs/dist/cropper.css';
import { Button } from '@mui/material';

const ImageUploadAndCrop = ({ onImageUpload }) => {
    const [image, setImage] = useState(null);
    const cropperRef = useRef(null);

    // 
    const handleFileChange = (e) => {
        const files = e.dataTransfer ? e.dataTransfer.files : e.target.files;
        if (files && files.length > 0) {
            const reader = new FileReader();
            reader.onload = () => setImage(reader.result); // 
            reader.readAsDataURL(files[0]);
        }
    };

    // 
    const handleCropImage = () => {
        const cropper = cropperRef.current?.cropper;
        if (cropper) {
            const croppedImage = cropper.getCroppedCanvas().toDataURL(); // Data URL
            onImageUpload(croppedImage); // 
        }
    };

    return (
        <div>
            {/*  */}
            <input type="file" onChange={handleFileChange} accept="image/*" />
            {image && (
                <div>
                    {/* Cropper */}
                    <Cropper
                        ref={cropperRef}
                        src={image}
                        style={{ height: 400, width: '100%' }}
                        aspectRatio={252 / 344} // 
                        guides={false}
                        cropBoxResizable
                        viewMode={1}
                    />
                    {/*  */}
                    <Button variant="contained" color="primary" onClick={handleCropImage}>
                        
                    </Button>
                </div>
            )}
        </div>
    );
};

ImageUploadAndCrop.propTypes = {
    onImageUpload: PropTypes.func.isRequired, // 
};

export default ImageUploadAndCrop;